import { FC, useEffect, useMemo, useState } from 'react';
import dayjs, { Dayjs } from 'dayjs';
import cx from 'classnames';
import { useDispatch, useSelector } from 'react-redux';

// MUI
import {
	Alert,
	Box,
	Button,
	FormControlLabel,
	MenuItem,
	Radio,
	RadioGroup,
	Select,
	SelectChangeEvent,
	Snackbar,
	Typography,
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';

// Interfaces
import { GoalInterface, GoalTask } from 'utils/interfaces/goal';

// Selectors
import { selectorGetCategories, selectorGetGoals } from 'redux/goals-service/selectors';

// Utils
import { getPriority } from 'utils/functions/getPriority';

// Acions
import { createGoal } from 'redux/goals-service/reducer';

// Hooks
import { useEnableEffect } from 'shared/hooks/useEnableEffect';

// Components
import { GoalFullTask } from 'shared/components/GoalFullTask';
import CreateGoalTaskModal from './CreateGoalTask';

// Styles
import './style.scss';

interface CreateGoalProps {
	isShown: boolean;
	setIsShown: (value: boolean) => void;
}

const CreateGoal: FC<CreateGoalProps> = ({ isShown, setIsShown }) => {
	const dispatch = useDispatch();
	const goals = useSelector(selectorGetGoals);
	const categories = useSelector(selectorGetCategories);
	const { enableCloseEffect, handleCloseModal } = useEnableEffect(setIsShown);
	const [isShowError, setIsShowError] = useState(false);
	const [tasks, setTasks] = useState<GoalTask[]>([]);
	const [priority, setPriority] = useState('4');
	const [category, setCategory] = useState('Personal');
	const [type, setType] = useState('Multi-step');
	const today = dayjs();
	const yesterday = dayjs().subtract(1, 'day');
	const [goal, setGoal] = useState<GoalInterface>({
		title: '',
		category,
		priority: +priority,
		start: yesterday.format('DD/MM/YYYY'),
		end: today.format('DD/MM/YYYY'),
		progress: 0,
		status: 'In progress',
		tasks: [],
		type,
	});
	const [isShowCreateTaskModal, setIsShowCreateTaskModal] = useState(false);

	const handleChangePriority = (event: SelectChangeEvent) => {
		setPriority(event.target.value as string);
		setGoal((prev: GoalInterface) => ({ ...prev, priority: +event.target.value }));
	};

	const handleChangeCategory = (event: SelectChangeEvent) => {
		setCategory(event.target.value);
		setGoal((prev: GoalInterface) => ({ ...prev, category: event.target.value }));
	};

	const handleClosePage = () => {
		handleCloseModal();
		setTasks([]);
		setGoal({
			title: '',
			category,
			priority: +priority,
			start: yesterday.format('DD/MM/YYYY'),
			end: today.format('DD/MM/YYYY'),
			tasks: [],
			type: '',
			progress: 0,
			status: 'In progress',
		});
	};

	const handleCreateGoal = () => {
		if (goals.find((item: GoalInterface) => item.title === goal.title)) {
			setIsShowError(true);
		} else {
			dispatch(
				createGoal({
					title: goal.title,
					category: goal.category,
					priority: goal.priority,
					start: goal.start,
					end: goal.end,
					tasks: goal.tasks,
					type: goal.type,
					progress: 0,
					status: 'In progress',
				})
			);
			handleClosePage();
		}
	};

	const handleChangeType = (event: React.ChangeEvent<HTMLInputElement>) => {
		setType((event.target as HTMLInputElement).value);
		setGoal((prev: GoalInterface) => ({ ...prev, type: event.target.value }));
	};

	const handleChangeStartDate = (newValue: Dayjs | null) => {
		setGoal((prev: GoalInterface) => ({ ...prev, start: newValue?.format('DD/MM/YYYY') || '' }));
	};

	const handleChangeEndDate = (newValue: Dayjs | null) => {
		setGoal((prev: GoalInterface) => ({ ...prev, end: newValue?.format('DD/MM/YYYY') || '' }));
	};

	useEffect(() => {
		setGoal((prev: GoalInterface) => ({ ...prev, tasks }));
	}, [tasks]);

	const isButtonDisabled = useMemo(
		() =>
			!!goal.title &&
			!!goal.category &&
			!!goal.priority &&
			!!goal.start &&
			!!goal.end &&
			!!goal.tasks &&
			!!goal.tasks.length,
		[goal]
	);

	if (!isShown) {
		return null;
	}

	return (
		<>
			<Box
				className={cx('create-goal', {
					close: enableCloseEffect,
				})}
			>
				<Box className="create-goal__title">
					<Box className="create-goal__title__goal-name flex">
						<Typography variant="subtitle">Goal Name:</Typography>
						<input
							type="text"
							placeholder="Reach level A1 in Spanish"
							onChange={event =>
								setGoal((prev: GoalInterface) => ({ ...prev, title: event.target.value }))
							}
						/>
					</Box>
					<Box className="create-goal__radio-type">
						<Typography variant="subtitle">Goal Type:</Typography>
						<RadioGroup
							defaultValue="Multi-step"
							name="radio-buttons-group"
							value={type}
							onChange={handleChangeType}
						>
							<FormControlLabel value="Simple" control={<Radio />} label="Simple" />
							<FormControlLabel value="Multi-step" control={<Radio />} label="Multi-step" />
						</RadioGroup>
					</Box>
					<Box className="create-goal__title__priority flex">
						<Typography variant="subtitle">Priority:</Typography>
						<Select value={priority} label="Priority" className="select" onChange={handleChangePriority}>
							<MenuItem value={1}>
								<Typography variant="body">{getPriority(1)}Lowest priority</Typography>
							</MenuItem>
							<MenuItem value={2}>
								<Typography variant="body">{getPriority(2)}Medium priority</Typography>
							</MenuItem>
							<MenuItem value={3}>
								<Typography variant="body">{getPriority(3)}High priority</Typography>
							</MenuItem>
							<MenuItem value={4}>
								<Typography variant="body">{getPriority(4)}Highest priority</Typography>
							</MenuItem>
						</Select>
					</Box>
				</Box>
				<Box className="create-goal__date">
					<Box className="create-goal__date-start flex">
						<Typography variant="subtitle">Start Date:</Typography>
						<DatePicker
							className="date"
							defaultValue={yesterday}
							format="DD/MM/YYYY"
							onChange={handleChangeStartDate}
						/>
					</Box>
					<Box className="create-goal__date-end flex">
						<Typography variant="subtitle">End Date:</Typography>
						<DatePicker
							className="date"
							defaultValue={today}
							format="DD/MM/YYYY"
							onChange={handleChangeEndDate}
						/>
					</Box>
				</Box>
				<Box className="create-goal__category">
					<Box className="create-goal__category-info">
						<Typography variant="subtitle">Category:</Typography>
						<Select value={category} label="Category" className="select" onChange={handleChangeCategory}>
							{categories.map(category => (
								<MenuItem value={category.title}>
									<Typography variant="body">{category.title}</Typography>
								</MenuItem>
							))}
						</Select>
					</Box>
				</Box>
				<Box className="create-goal__tasks">
					{!!tasks.length &&
						tasks.map((item: GoalTask) => <GoalFullTask title={item.title} time={item.time} />)}
				</Box>
				{type === 'Simple' ? (
					<Button
						size="medium"
						color="primary"
						variant="buttonLight"
						className="create-goal__add-task"
						onClick={() => setIsShowCreateTaskModal(true)}
					>
						<Typography variant="body" className="goals__header-title">
							Add a task
						</Typography>
					</Button>
				) : (
					<Button
						size="medium"
						color="primary"
						variant="buttonLight"
						className="create-goal__add-task"
						onClick={() => setIsShowCreateTaskModal(true)}
					>
						<Typography variant="body" className="goals__header-title">
							Add a subgoal
						</Typography>
					</Button>
				)}

				<Box className="create-goal__actions">
					<Button color="secondary" variant="buttonLight" size="small" onClick={handleClosePage}>
						<Typography variant="body">Cancel</Typography>
					</Button>
					<Button
						color="primary"
						variant="buttonDark"
						size="small"
						onClick={handleCreateGoal}
						className={cx({
							isDisabled: !isButtonDisabled,
						})}
						disabled={!isButtonDisabled}
					>
						<Typography variant="body">Save goal</Typography>
					</Button>
				</Box>
				<CreateGoalTaskModal
					isShow={isShowCreateTaskModal}
					setIsShow={setIsShowCreateTaskModal}
					setTask={setTasks}
					tasks={tasks}
				/>
			</Box>
			<Snackbar open={isShowError} autoHideDuration={4000} onClose={() => setIsShowError(false)}>
				<Alert onClose={() => setIsShowError(false)} severity="error" sx={{ width: '100%' }}>
					<Typography variant="subtitle">There is already a goal with that name!</Typography>
				</Alert>
			</Snackbar>
		</>
	);
};

export default CreateGoal;
